@import 'ScssHelpers/helpers-all';

.download_popup__content {
	background-color: $white_2;

	@include bp($point_2, min) {
		padding: 0 13.6rem 8.4rem;
	}

	@include bp($point_3) {
		padding: 8rem var(--gl-indent) 5rem;
	}
}

.download_popup__title {
	@include h2;

	font-family: $font_2;
	font-weight: 700;
	text-align: center;

	@include bp($point_2, min) {
		&:not(:last-child) {
			margin-bottom: 2rem;
		}
	}

	@include bp($point_3) {
		&:not(:last-child) {
			margin-bottom: 4rem;
		}
	}
}

.download_popup__descr {
	@include text;

	font-weight: 700;
	text-align: center;
	margin: 0 auto;

	@include bp($point_2, min) {
		width: 70%;
		font-size: 2rem;

		&:not(:last-child) {
			margin-bottom: 4.4rem;
		}
	}

	@include bp($point_3) {
		&:not(:last-child) {
			margin-bottom: 4rem;
		}
	}
}

.download_popup__form_row_w {
	@include bp($point_2, min) {
		&:not(:last-child) {
			margin-bottom: 4rem;
		}

		&:nth-last-child(2) {
			margin-bottom: 5.2rem;
		}
	}

	@include bp($point_3) {
		&:nth-last-child(2) {
			margin-bottom: 3.2rem;
		}
	}
}

.download_popup__form_row {
	@include flex_block(row, wrap, flex-start, flex-start, flex-start);

	@include bp($point_2, min) {
		margin: 0 -2.6rem -2.6rem 0;
	}
}

.download_popup__form_col {
	@include bp($point_2, min) {
		width: 50%;
		padding: 0 2.6rem 2.6rem 0;
	}

	@include bp($point_3) {
		width: 100%;
		margin-bottom: 2rem;
	}
}

.download_popup__form_btn_w {
	@include flex_block(row, wrap, center, center, center);
}
