@import 'ScssHelpers/helpers-all.scss';

.testimonial_block {
	@include bp($point_2, min) {
		padding: 9rem var(--gl-indent);
	}

	@include bp($point_3) {
		padding: 5rem var(--gl-indent);
	}
}

.testimonial_block__header {
	width: 100%;

	text-align: center;

	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-bottom: 5.2rem;
		}

		@include bp($point_3) {
			margin-bottom: 4.6rem;
		}
	}

	h1 {
		font-family: $font_2;

		@include h2;

		&:not(:last-child) {
			@include bp($point_2, min) {
				margin-bottom: 1.4rem;
			}

			@include bp($point_3) {
				margin-bottom: 0.8rem;
			}
		}
	}

	p {
		@include text;
	}
}

.testimonial_block__body {
	overflow: visible;
}

.testimonial_block__card {
	height: auto;

	@include bp($point_2, min) {
		max-width: 38.5rem;
	}

	@include bp($point_3) {
		max-width: 29rem;
	}
}

.testimonial_block__wrapper {
	display: flex;
	flex-direction: column;

	height: 100%;

	background-color: $green;

	border-radius: $gl_radius;

	box-shadow: 1rem 1rem 8rem rgba(0, 0, 0, 0.2);

	@include bp($point_2, min) {
		padding: 8.4rem 3.1rem 11.5rem 3.1rem;
	}

	@include bp($point_3) {
		padding: 7.4rem 2.4rem;
	}

	h6 {
		@include subtitle;

		&:not(:last-child) {
			@include bp($point_2, min) {
				margin-bottom: 4.8rem;
			}

			@include bp($point_3) {
				margin-bottom: 1.2rem;
			}
		}
	}

	h4 {
		font-family: $font_2;

		@include h4;

		&:not(:last-child) {
			@include bp($point_2, min) {
				margin-bottom: 1rem;
			}

			@include bp($point_3) {
				margin-bottom: 1.2rem;
			}
		}
	}

	p {
		display: block;
		flex: 1 1 100%;

		margin-bottom: 4.8rem;

		font-weight: 400;

		@include bp($point_2, min) {
			font-size: 1.8rem;
			line-height: 1.3;
			letter-spacing: 0.03em;
		}

		@include bp($point_3) {
			font-size: 1.4rem;
			line-height: 1.36;
			letter-spacing: 0.04em;
		}
	}

	ul {
		@include bp($point_3) {
			margin-left: 0.4rem;
		}
	}

	li {
		font-weight: 500;

		@include bp($point_2, min) {
			font-size: 1.5rem;
			line-height: 1.34;
			letter-spacing: 0.02em;
		}

		@include bp($point_3) {
			font-style: italic;
			font-size: 1.2rem;
			line-height: 1.4;
		}
	}
}

.testimonial_block .swiper-wrapper {
	@include bp($point_2, min) {
		padding-bottom: 7rem;
	}
}

.testimonial_block .swiper-scrollbar {
	@include bp($point_3) {
		display: none;
	}
}