@import 'ScssHelpers/helpers-all';

.popup {
	position: fixed;
	top: 0;
	left: 0;

	display: flex;
	align-content: stretch;
	width: 100%;
	height: 100vh;

	overflow-x: hidden;
	overflow-y: auto;

	background-color: rgba($black_2, 0.8);
	transition: opacity 0.3s ease;
	will-change: opacity;

	@include bp($point_2, min) {
		padding: 2rem;
	}

	&:not(.popup--open_state) {
		z-index: 0;

		opacity: 0;
		pointer-events: none;
	}

	&.popup--open_state {
		z-index: z-index(popup);

		opacity: 1;
		pointer-events: initial;
	}
}

.popup_in {
	position: relative;

	width: $page_width;
	max-width: 100%;
	margin: auto;
	background-color: transparent;

	.popup_download & {
		width: 133.3rem;

		@include bp($point_2, min) {
			padding-top: 20.4rem;
		}
	}
}

.popup_close {
	position: absolute;
	top: 1rem;
	right: 1rem;
	z-index: 5;

	width: 2.8rem;
	height: 2.8rem;

	transition: opacity 0.3s ease;
	will-change: opacity;
	cursor: pointer;

	.popup_download & {
		@include bp($point_2, min) {
			top: 26.2rem;
			right: 7.1rem;
		}

		@include bp($point_3) {
			top: 2rem;
			right: 2rem;
		}
	}

	&:hover,
	&:focus {
		opacity: 0.8;
	}

	&::before,
	&::after {
		position: absolute;
		//top: 1.5rem;
		left: 0;

		height: 2px;
		width: 3.4rem;

		content: '';
		background-color: $coral_2;

		.popup_close:hover & {
		}
	}

	&::before {
		transform: rotate(45deg);
	}

	&::after {
		transform: rotate(-45deg);
	}
}

.popup_decor__picture {
	position: absolute;
	z-index: -1;
	top: 0;
	left: 0;

	display: block;

	width: 100%;
}

.popup_decor__img {
	@include cover_img;
}
